<template>
  <div class="main_navs">
    <Header></Header>
      <div class="container">

        <div class="on_test_title">Shахsiyat tеsti vа ishdаn qаnоаtlаnish</div>
        <div class="on-test">
          <div class="one_test_progress">
            <div class="on-test-count">
              Вопросы  {{ sliderValue }}/{{max}}
            </div>
            <div class="on-test-range">
              <range-slider
                  class="slider"
                  min="1"
                  :max="max"
                  step="1"
                  v-model="sliderValue">
              </range-slider>
            </div>
            <div class="on-test-card">
                <div>
                  <div class="on-test-quiz">
                    Mеn hаr dоim оlingаn ахbоrоt аniqligigа ishоnch hоsil qilishgа hаrаkаt qilаmаn
                  </div>
                  <div class="on-test-values" :class="{'active':isActive}">
                      <button v-for="(item,index) in 10" :key="index" @click="isActive=!isActive">
                        {{item}}
                      </button>
                  </div>
                  <div class="on-test-button">
                    <button class="prev" @click="prevStart()">

                      <span>Odldingisi</span>

                    </button>
                    <button class="next" @click="nextStart()">
                      <span>Keyngisi</span>

                    </button>
                  </div>
                </div>
            </div>
          </div>
        </div>
      </div>
    <Footer></Footer>
  </div>
</template>
<script>
import Header from "../../components/Header";
import Footer from "../../components/Footer";
import RangeSlider from 'vue-range-slider'
// you probably need to import built-in style
import 'vue-range-slider/dist/vue-range-slider.css'

export default {
  components: {Footer, Header,RangeSlider},
  data(){
    return{
      sliderValue: 1,
      value:0,
      activeIndex: 0,
      max:40,
      isActive:false
    }
  },
  methods:{
    nextStart(){
      if(this.sliderValue<40){
        this.sliderValue++
      }
    },
    prevStart(){
      if(this.sliderValue>1){
        this.sliderValue--
      }
    }
  }
}
</script>
